.custom {
  min-height: 68vh;
  width: 100%;
  min-height: 450px;
  position: relative;
}

.custom--image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.custom--text {
  position: absolute;
  right: 20%;
  top: 15%;

  font-size: 2.5em;
}

.custom--button {
  position: absolute;
  right: 20%;
  top: 25%;

  border-radius: 50%;
  background-color: black;
  color: white;

  padding: 1em;

  font-size: 1.2em;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
