footer {
  height: 30vh;
  background-color: white;

  display: flex;
  justify-content: space-around;
  align-items: center;

  padding: 3em 0;

  font-weight: 300;
}

.footer--logo--section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer--logo--image {
  height: auto;
  width: 15vw;
}

.footer--links--section {
  display: flex;
  flex-direction: column;
}

.footer--links {
  color: black;
}
